import React, { Component } from 'react';
import './App.css';
import Header from './Header';
import Footer from './Footer';
import RulingBox from './RulingBox';

class App extends Component {
  render() {
    return (
      <div className="App">
        <Header />
        <main>
          <RulingBox />
          <RulingBox />
          <RulingBox />
          <RulingBox />
        </main>
        <Footer />
      </div>
    );
  }
}

export default App;



